* {
  margin: 0;
  padding: 0;
  font-family: monaco;
}

li {
  list-style: none;
}
.app {
  width: 100%;
  min-height: 100vh;
  background-color: #f5f5f5;
  padding: 20px 0;
}

.board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  margin: 0 auto;
  padding: 20px;
}

.board li {
  width: 30px;
  height: 30px;
  margin: 2px;
  -webkit-box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
          box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  background-color: gray;
  text-align: center;
  line-height: 30px;
  font-size: 20px;
  font-weight: bold;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

.mode button {
  margin: 0 auto;
  display: block;
  width: 100px;
  height: 40px;
  background-color: #fff;
  border: 0;
  border-radius: 0;
  -webkit-box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
          box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  outline: none;
  font-size: 20px;
  color: red;
}
.mode button:active {
  -webkit-transform: translateY(2px);
      -ms-transform: translateY(2px);
          transform: translateY(2px);
}

.mode {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-top: 20px;
}

.mode button {
  margin: 0 5px;
  color: #666;
}

p {
  text-align: center;
  padding-top: 20px;
  font-size: 40px;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}



